<template>
  <div class="top-container">
    <!-- 顶部 -->
    <div class="left-box">
      <div class="icon-wrapper">
      <!-- 三个小圆点 -->
        <span class="iconfont icon-home" @click="toHomeClick()"></span>
        <!-- <span class="iconfont icon-sami-select"></span> -->
        <span class="iconfont icon-full-screen" @click="getFullCreen(btnStatus)"></span>
      </div>
      <!-- 前进和后退 -->
     <!--  <div class="history-wrapper">
        <span class="iconfont icon-arrow-lift"></span>
        <span class="iconfont icon-arrow-right"></span>
      </div> -->
    </div>
    <!-- 搜索栏 -->
    <div class="right-box">
       <div class="el-input el-input--small el-input--prefix">
        <input type="text" autocomplete="off" placeholder="搜索" v-model="inputVal" class="el-input__inner" @keyup.enter="toResult">
        <span class="el-input__prefix">
          <i class="el-input__icon el-icon-search"></i>
        </span>
       </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'top',
  data () {
    return {
      // 输入的内容
      inputVal: '',
      query: '',
      n: 0,
      btnStatus: 0
    }
  },
  created () { },
  methods: {
    //前往搜索页面
    toResult () {
      if (this.inputVal === '') {
        //提示用户输入内容
        this.$message.warning('请输入内容!')
      } else {
        //携带数据去搜索页面
        // console.log(this.inputVal);//此为输入的内容
        this.$router.push(`/result?q=${this.inputVal}`)
      }
    },
    //小红点 返回首页
    toHomeClick () {
      if ('/discover' === this.$route.path) {
        return
      }
      this.$router.push(`/discover`)
    },
    //小绿点 点击实现全屏和退出全屏
    getFullCreen (btnStatus) {
      this.btnStatus = this.btnStatus === 0 ? 1 : 0
      this.n++
      this.n % 2 == 0 ? this.outFullCreen(document) : this.inFullCreen(document.documentElement)
    },

    inFullCreen (element) {
      let el = element
      let rfs = el.requestFullScreen || el.webkitRequestFullScreen || el.mozRequestFullScreen || el.msRequestFullScreen
      if (typeof rfs != 'undefined' && rfs) {
        rfs.call(el)
      } else if (typeof window.ActiveXObject != 'undefined') {
        let wscript = new ActiveXObject('WScript.Shell')
        if (wscript != null) {
          wscript.SendKeys('{F11}')
        }
      }
    },

    outFullCreen (element) {
      let el = element
      let cfs = el.cancelFullScreen || el.webkitCancelFullScreen || el.mozCancelFullScreen || el.exitFullScreen
      if (typeof cfs != 'undefined' && cfs) {
        cfs.call(el)
      } else if (typeof window.ActiveXObject != 'undefined') {
        let wscript = new ActiveXObject('WScript.Shell')
        if (wscript != null) {
          wscript.SendKeys('{F11}')
        }
      }
    }
  }
}
</script>
<style lang='scss'>
.top-container {
  z-index: 999;
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  background-color: #f9f9f9;
  height: 60px;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  .left-box {
    display: flex;
    .icon-wrapper {
      margin-right: 80px;
      .iconfont {
        cursor: pointer;
        margin-right: 10px;
        font-size: 12px;
        width: 18px;
        height: 18px;
        display: inline-block;
        text-align: center;
        line-height: 18px;
        &.icon-home {
          background-color: #dd6d60;
          border-radius: 50%;
        }
        &.icon-sami-select {
          background-color: #dcc060;
          border-radius: 50%;
        }
        &.icon-full-screen {
          background-color: #84bb58;
          border-radius: 50%;
        }
        &::before {
          opacity: 0;
          transition: 0.2s;
        }
        &:hover::before {
          opacity: 1;
        }
      }
    }
    .history-wrapper {
      display: flex;
      align-items: center;
      .iconfont {
        width: 35px;
        height: 35px;
        font-size: 30px;
        text-align: center;
        line-height: 35px;
        color: gray;
        border-radius: 50%;
        transition: 0.2s;
        cursor: pointer;
        &:first-child {
          margin-right: 10px;
        }
        &:hover {
          background-color: #ebebeb;
        }
      }
    }
  }
  .right-box {
    display: flex;
    align-items: center;
  }
}
</style>
