import Vue from 'vue'
import App from './App.vue'
import router from './router'
import ElementUI from 'element-ui'//引用element
import 'element-ui/lib/theme-chalk/index.css' //引入element 样式
// 导入全局初始化样式
import './assets/index.css'

import axios from 'axios'

/* mixin 全局混入 */
Vue.mixin({
  data () {
    return {
      value2: 1012
    }
  },
  methods: {
    overallMixin () {
      console.log(this.value2, '+这是通过全局混入mixin 传递的message');
    }
  }
})

Vue.prototype.FFaxios = axios
Vue.use(ElementUI) //使用
Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
